import Link from "next/link";
import { ArrowRight, Building2, ShoppingBag, Landmark, BookOpen } from "lucide-react";

export default function Home() {
  const portals = [
    {
      title: "Company Profile",
      description: "Discover our corporate identity, vision, and mission.",
      href: "http://localhost:3001",
      icon: <Building2 className="w-8 h-8 text-primary" />,
      color: "bg-blue-50 dark:bg-blue-950/30",
      borderColor: "border-blue-200 dark:border-blue-800",
    },
    {
      title: "E-Commerce",
      description: "Browse our products and seamless shopping experience.",
      href: "http://localhost:3002",
      icon: <ShoppingBag className="w-8 h-8 text-emerald-600 dark:text-emerald-400" />,
      color: "bg-emerald-50 dark:bg-emerald-950/30",
      borderColor: "border-emerald-200 dark:border-emerald-800",
    },
    {
      title: "Government Portal",
      description: "Access public services and city information.",
      href: "http://localhost:3003",
      icon: <Landmark className="w-8 h-8 text-amber-600 dark:text-amber-400" />,
      color: "bg-amber-50 dark:bg-amber-950/30",
      borderColor: "border-amber-200 dark:border-amber-800",
    },
    {
      title: "Product Catalog",
      description: "Explore our comprehensive directory of offerings.",
      href: "http://localhost:3004",
      icon: <BookOpen className="w-8 h-8 text-purple-600 dark:text-purple-400" />,
      color: "bg-purple-50 dark:bg-purple-950/30",
      borderColor: "border-purple-200 dark:border-purple-800",
    },
  ];

  return (
    <div className="min-h-screen bg-background flex flex-col items-center justify-center p-6 lg:p-12 relative overflow-hidden">
      {/* Background decoration */}
      <div className="absolute top-0 inset-x-0 h-96 bg-gradient-to-b from-primary/5 to-transparent -z-10" />
      <div className="absolute top-[-10%] left-[-10%] w-[40%] h-[40%] rounded-full bg-primary/5 blur-3xl -z-10" />
      <div className="absolute bottom-[-10%] right-[-10%] w-[40%] h-[40%] rounded-full bg-emerald-500/5 blur-3xl -z-10" />

      <div className="max-w-5xl w-full space-y-12">
        <div className="text-center space-y-4 animate-fade-up">
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold tracking-tight text-foreground">
            Welcome to the Workspace
          </h1>
          <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
            Select an application to proceed to the designated environment.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 lg:gap-8 max-w-4xl mx-auto">
          {portals.map((portal, index) => (
            <Link 
              key={portal.href} 
              href={portal.href}
              className="group"
            >
              <div 
                className={`relative h-full flex flex-col p-8 rounded-2xl border ${portal.borderColor} ${portal.color} transition-all duration-300 hover:shadow-lg hover:-translate-y-1 overflow-hidden`}
                style={{ animationDelay: `${index * 150}ms` }}
              >
                <div className="absolute top-0 right-0 p-6 opacity-10 group-hover:opacity-20 transition-opacity transform group-hover:scale-110 duration-500">
                  {portal.icon}
                </div>
                
                <div className="mb-6 p-4 bg-background/50 backdrop-blur-sm rounded-xl w-fit border border-background/20">
                  {portal.icon}
                </div>
                
                <div className="space-y-2 flex-grow">
                  <h2 className="text-2xl font-bold text-foreground group-hover:text-primary transition-colors">
                    {portal.title}
                  </h2>
                  <p className="text-muted-foreground leading-relaxed">
                    {portal.description}
                  </p>
                </div>
                
                <div className="mt-8 flex items-center text-sm font-medium text-foreground group-hover:text-primary transition-colors">
                  <span>Launch Application</span>
                  <ArrowRight className="ml-2 w-4 h-4 transition-transform group-hover:translate-x-1" />
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
}
